
.prizeRecordDetail {
  min-height: 100vh;
  background: #f6f6f6;
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      justify-content: center;
      margin: 8px 0 0;
      .information {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        .information-top {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          background: #fff;
          .activity,
          .award_type {
            width: 90%;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #f7f7f7;
            padding: 14px 0 15px;
            .label {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #2c2c2c;
              line-height: 20px;
            }
            .message {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #999999;
              line-height: 20px;
            }
          }
        }
        .information-center {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          background: #fff;
          margin: 8px 0 0;
          .winning_state,
          .prize,
          .winning_time {
            width: 90%;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #f7f7f7;
            padding: 14px 0 15px;
            .label {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #2c2c2c;
              line-height: 20px;
            }
            .message {
              font-size: 14px;
              font-family: PingFang SC-中等, PingFang SC;
              font-weight: normal;
              color: #999999;
              line-height: 20px;
            }
          }
        }
        .information-bottom {
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          background: #fff;
          margin: 8px 0 0;
        }
        .award_case {
          width: 90%;
          display: flex;
          justify-content: space-between;
          padding: 14px 0 15px;
          .label {
            font-size: 14px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #2c2c2c;
            line-height: 20px;
          }
          .message {
            font-size: 14px;
            font-family: PingFang SC-中等, PingFang SC;
            font-weight: normal;
            color: #999999;
            line-height: 20px;
          }
        }
      }
    }
  }
}
